<template>
  <!-- 
    1. 화면이 로딩 되면, 어떤 공간에서 필요한 데이터를 먼저 가져온다.
    2. 그 데이터를 가지고 왔다면, 어딘가에 저장한다 > 변수
    (변수 저장 시 this.변수명 = 데이터값)
    변수는 data 안에 설정
    - 가지고 온 데이터 + 저장한 데이터가 잘 나오는지 console 창에 꼭 확인하기!!!
    3. 반복해야 될 요소(div가 되었던 p가 되었던 전체 부모요소를 반복한다.)
    v-for="작명 in 변수명" :key=작명
    변수명에 있던 모든 데이터는 작명한 변수에 모두 들어간다.
    listItem[0].TITLE / listItem[1].MAIN_IMG_THUMB
    이렇게 생긴 값들이
    작명.TITLE / 작명.MAIN_IMG_THUMB 로 작성시 모두 적용된다.
    
    -->
    <!-- #4b473c -->
    <div class="w-full bg-[#f7f8f9] px-[2%] pb-12 pt-32 box-border">
      <div class="max-w-7xl mx-auto box-border my-3">
        <router-link :to="'/'" class="cursor-pointer">
          <img src="@/assets/main_logo2.png" alt="logo">
        </router-link>
      </div>
      <div class="max-w-7xl mx-auto flex flex-wrap gap-x-[1%]">
        <ul class="flex justify-center bg-white rounded-md border box-border p-4 basis-full mb-4 flex-wrap gap-x-4">
          <li @click="cateName='전체'" class="cursor-pointer text-xs md:text-base basis-[20%] sm:basis-[15%] md:basis-[10%] lg:basis-[8%] text-center mb-2 sm:mb-0" :class="cateName === '전체' && 'font-extrabold text-orange-500'">전체</li>
          <li @click="cateName= e.tourSe " v-for="e in cateList" :key="e" class="cursor-pointer text-xs md:text-base basis-[20%] sm:basis-[15%] md:basis-[10%] lg:basis-[8%] text-center mb-2 sm:mb-0" :class="cateName === e.tourSe && 'font-extrabold text-orange-500'"> {{ e.tourSe }} </li>
          <!-- 밖에 데이터를 2개 쓸때는 {{ e.tourSe }} 안에 변수 값을 넣어주고 class = 나 click = 값 안에는 {{  }} 안에 말고 그냥 e.tourSe를 써줘야한다. -->
        </ul>

        <div class="relative mt-4">
          <swiper
            :modules="modules"
            :slides-per-view="2"
            :breakpoints="{ 640:{ slidesPerView:3 } }"
            :space-between="50"
            :scrollbar="{ draggable: true }"
            :mousewheel="true"
            @swiper="onSwiper"
            @slideChange="onSlideChange" 
          >
          <SwiperSlide v-for="e in cateItem" :key="e" class="bg-white !w-[30%] !h-[500px] sm:!w-[48%] lg:!w-[30%] my-4 box-border border rounded-md p-5 cursor-pointer">
            <h3 class="mb-4 text-lg">{{ e.tourNm }}</h3>
            <img :src="e.thumbImg" :alt="e.tourNm" class="w-[400px] sm:w-full mb-6 mx-auto">
            <ul>
              <li>전화번호 : {{ e.telno }}</li>
              <li>지역 : {{ e.areaSe }}</li>
              <li><button v-if="e.tourUrl !== ''" @click="open_page(e.hmpg)">홈페이지</button></li>
            </ul>
            <router-link :to="'/detail/'+(e.tourNo)" @click="ResultList(e.tourNo)">상세내용</router-link> 
          </SwiperSlide>
          </swiper>
        </div>
        <!-- <div v-for="(e) in cateItem" :key="e" class="bg-white basis-full sm:basis-[49%] lg:basis-[32.5%] mb-4 box-border border rounded-md p-5 cursor-pointer"> -->
          
          <!-- <p @click="ResultList(e.tourNo)">테스트</p> -->
        <!-- </div> -->
        <div class="basis-full bg-white flex justify-around">
          <ul class="flex justify-center">
            <li v-for="e in totalCount / pageList" :key="e" class="cursor-pointer" :class="e === page && 'font-extrabold text-orange-500'" @click="page = e; getItem()">{{ e }}</li>
          </ul>
        </div>
      {{ page }}
    </div>
  </div>
</template>

<script>
// https://tour.chungbuk.go.kr/openapi/tourInfo/attr.do
// import axios from 'axios';
// @/assets/attractionAPI.json
import information from '@/assets/attractionAPI.json'
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import { Mousewheel, Scrollbar } from 'swiper';
export default {
  name: 'App',
  components: {
    Swiper,
    SwiperSlide
  },
  setup() {
    const onSwiper = (swiper) => {
      console.log(swiper);
    };
    const onSlideChange = () => {
        console.log('slide change');
    };
    return {
      onSwiper,
      onSlideChange,
      modules: [Mousewheel, Scrollbar],
    }
  },
  data() {
    return {
      pageList: 30,
      listItem: information.result, // @/assets/attractionAPI.json에서 import 해온 것을 information 으로 이름을 지정해주고 여기 listItem에 information을 저장시킨다. 그러나 주의해야할건 내가 처음에 listItem에 information 만 주었을때는 에러가 떴다 왜냐하면 데이터는 information API 파일 안에 result안에 있기 때문이다. 그래서 listItem에 information이 아닌 information.result로 지어줘야 한다.
      page: 1,
      totalCount : 0,
      cateName : "전체",
    }
  },
  methods:{
    ResultList(e){ // ResultList(e)는 위에 ResultList(e.UC_SEQ)를 대신 가져온것이다 따라서 e 값은 여기서 e.UC_SEQ를 뜻한다. 만약 e 대신 다른 문자인 abcde를 넣어도 된다 그러면 abcde가 e.UC_SEQ를 받아온다. 따라서 ResultList 함수를 템플릿에서 입력하고 클릭해줘야 session storage로 호출이된다.
      return this.listItem.filter((data)=>{
        if(data.tourNo === e){
          return this.session(data) // session을 실행시키세요
          // return console.log(data)
        }
      })
    },
    session(data){
      sessionStorage.setItem('data',JSON.stringify(data)); // 이 데이터를 JSON 형태로 sessionStorage에 넣는다.
    },
    open_page(url){
      window.open(url);
    },
    // 원래는 window.opem(url)을 눌러야 열리는데 methods에 open_page(url)로 대체하도록 했다.
    // getItem: function(){
    //   axios.get(`@/src/assets/attractionAPI.json`)
    // .then((res)=>{
    //   this.listItem = res.data
    //   console.log(res.data)
    // })
    // .catch((error)=>{
    //   console.log(error)
    // })
    // }
  },
  mounted(){
    console.log("Create 이후 실행")
  },
  created(){
    console.log("먼저 실행")
    console.log(this.listItem)

    // axios.get(`@/src/assets/attractionAPI.txt`)
    // .then((res)=>{
    //   console.log(res.data);
    //   this.totalCount = res;
    //   this.listItem = res
    // })
    // .catch((error)=>{
    //   console.log(error)
    // })
    

  },
  computed: {
    cateList(){
      return this.listItem.filter((item,i)=>{
        return(
          this.listItem.findIndex((item2)=>{
            return item.tourSe === item2.tourSe
          }) === i
        )
      })
    },
    // 카테고리에 수영구, 영도구 등등이 있는데 수영구가 여러개 있으면 나머지는 없애고 하나만 가져온다.
    cateItem(){
      return this.listItem.filter((data)=>{

        if(this.cateName !== '전체'){
          return data.tourSe === this.cateName
        }else{
          return data.tourSe
        }
        // 카테고리 이름이 전체가 아니면 그와 맞는 카테고리 자료만 보여주고 전체이면 전체 다 보여준다.
      })
    }
  },
  // computed - 탬플릿 내 값을 연산할 때 사용 - 같은 값일 때는 연산하지 않는다.
  // watch - 같은 값이어도 다시 연산
  // 값이 바뀔 일이 없다면 coumputed 사용
  // created - 인스턴스가 작성된 후 동기적으로 호출
  // mounted - 부모,자식 관계의 컴포넌트가 랜더링 될 때 created 다음으로 호출 > 자식 / 부모 순으로 실행
  // ※ 뷰는 create를 생성하고 DOM에 부착(mounted) 업데이트(update) 없어지는 (destory) 4가지 과정을 거친다.
}
</script>

<style>

</style>